.container {
  max-width: 1280px;
  height: 100%;
  margin: 0 auto;
  padding: 0 20px;
}

.stretched-link {
  position: absolute;
  z-index: 99;

  display: block;

  inset: 0;
}

.link {
  font-size: 1.8rem;
  color: #000000;
  font-weight: 500;
  text-decoration: underline;
}
